@import url('https://fonts.googleapis.com/css?family=Amaranth|Syncopate');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Syncopate;
    color: #fff;
}

body {
    background-image: url("img/bg_feia.png");
    background-size: 100%;
    background-repeat: repeat;
}

@media (orientation: portrait) {
    body {
            background-image: url("img/bg_feia.png");
            background-size: 150vh;
            background-repeat: repeat;
    }
}

#logoapoio {
    background: rgba(255, 200, 200, 0.2);
    backdrop-filter: blur(10px);
    margin: 0 auto;
}

#box {
    display: flex;
    align-content: center;
}


/* NAVBAR */

header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    width: 100vw;
    padding: 0 2rem;
    background: rgba(255, 200, 200, 0.2);
    backdrop-filter: blur(10px);
    color: #fff;
}

header .nav-btn{
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: #fff;
    display: none;
}

nav a{
    margin: 0 2rem;
    color: #fff;
    text-decoration: none;

}

nav a:hover{
    color: #faa;
}

@media (orientation: portrait) {
    header .nav-btn{
        display: inline-flex;
    }

    header nav{
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        display: flex;
        align-items: center;
        flex-direction: column;
        gap: 1.5rem;
        padding: 10rem;
        background: rgb(0, 0, 0);
        backdrop-filter: blur(10px);
        transition: 1s;
        transform: translateY(-100vh);
    }

    header .responsive-nav{
        transform: none;
        z-index: 1;
    }
}



/*manifesto*/

.manifesto {
    width: 100vw;
    margin: 0 auto;
    font-family: Arial;
    display: flex;
    flex-direction: column;
}

.manifesto-container {
    width: 750px;
    display: flex;
    justify-content: center;
    flex-direction: row;
}


.slide {
   display: flex;
   justify-content: center;
}

.slide img {
    height: 80vh;
}
/*iphone*/
@media (max-width: 500px){
    body {
        background-image: none;
        background-color: black;
    }

    .slide{
        width: 100vw;
    }

    .slide img {
        width: 350px;
        height: 350px;
        padding: 10px;
    }
    .manifesto-container {
        width: 100vw;
        flex-direction: column;
        margin: 0 auto;
    }
}

/*ipad*/
@media (min-width: 501px) {
    .slide img{
        width: 30vh;
        height: 30vh;
    }
}